@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(19,19,19);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navbar-container {
    height: 60px;
    margin: 20px;
    grid-column: 1/7;
}

.nav-item {
    display: inline-block;
    padding:  0 20px;
    margin: 0 20px;
    font-size: 22px;
    border-radius: 17px;
    transition: 0.3s;
    font-weight: bold;
}

.nav-item:hover {
    background-color: rgb(255, 119, 56);
    border-radius: 10px;
}
.app-container {
    display: grid;
    grid-template-columns: repeat(6, 1fr);

    margin: auto;
    max-width:  1800px; /* 1200px; */
    padding: 100px 0; 
}
.main-container {
    margin: 20px;
    grid-column: 1/5;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}
.section-container {
    height: 29vh;
    width: 45vh;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin: 30px auto;
    border-radius: 20px;
    overflow: hidden;
    box-shadow: 0px 5px 45px -17px rgba(87, 87, 87, 0.75);
    -webkit-box-shadow: 0px 5px 45px -17px rgba(87, 87, 87, 0.75);
    -moz-box-shadow: 0px 5px 45px -17px rgba(87, 87, 87, 0.75);
    transition: ease 0.5s;
}

.section-container:hover {
    height: 35vh;
}

.section-img {
    position: relative;
    max-width: 100%;
    max-height: 100%;
    height: 100%;
    width: 100%;
    grid-column: 1/3;
    grid-row: 1/2;
}

.section-data {
    grid-column: 1/3;
    background-color: rgb(204, 204, 204);
    text-align: center;
    line-height: 20px;
}

.article-title {
    margin: 20px;
}

.article-info {
    font-weight: bold;
    color: rgba(109, 109, 109, 0.733);
}
.side-container {
    height: 800px;
    top:80px;
    margin: 20px;
    grid-column: 5/7;
    position: -webkit-sticky;
    position: sticky;
    background-color: rgba(240, 248, 255, 0.664);
    border-radius: 30px;
}

.side-content {
    margin: 20px;
}

.side-most-value {
    margin: 50px 20px;
}

.side-publicity {
    margin: 20px;
}


.publicity-1 {
    border: 2px solid black;
    height: 200px;
}
.hero-container {
    height: 100vh;
    grid-column: 1/7;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(2, 1fr);
    position: relative;
    background-image: url("https://i.redd.it/9jy01y6i0jq71.png");
    border-bottom-left-radius:3%;
    border-bottom-right-radius:3%;
    margin-bottom: 100px;
}

.hero-title {
    margin: auto;
    color: rgb(255, 255, 255);
    font-size:69px;
    grid-column: 3/5;
    grid-row: 1/3;
    -webkit-text-stroke: 1px black;
}

.title-description {
  grid-column: 2/6;
  grid-row: 2;
  text-align: center;
  color: rgb(255, 255, 255);
  font-size:50px;
  -webkit-text-stroke: 1px black;
}

.humble-bundle {
  grid-column: 2/6;
  grid-row: 2;
  text-align: center;
  font-family: 'Bebas Neue', cursive;
  font-size:70px;
  margin-top: 12%;
  -webkit-text-stroke: 1px black;
  transition: 0.3s;
}

.humble-bundle:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-text-stroke: 1px black;
  color: rgb(231, 92, 92);
}

a { color: white; text-decoration: none; }

.arrow {
  text-align: center;
  margin: 8% 0;
}
.bounce {
  -webkit-animation: bounce 2s infinite;
  animation: bounce 2s infinite;
}

@-webkit-keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
  }
  60% {
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
  }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
  }
  60% {
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
  }
}
.footer-container {
    position: relative;
    background-color: rgb(51, 51, 51);
    height: 20vh;
    margin-top: 110px;
    grid-column: 1/7;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    color: rgb(231, 217, 217);
}

.footer-title {
    text-align: center;
    margin-top: 12%;
    grid-column: 1/3;
}

.copyright {
    grid-column: 5/7;
    text-align: center;
}
